export default {
  data() {
    return {
      // 角色列表
      rolesList: [],
      // 权限列表
      rightsList: [],
      // 展示区是否可视
      dialogVisible: false,
      // 树形结构数据获取
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      // 默认选中的ID值
      defKeys: [],
      // 即将分配权限的id
      roleId: [],
      // 添加角色对话框是否可视
      roleDialogVisible: false,
      // 编辑角色对话框是否可视
      editDialogVisible: false,
      // 添加验证规则
      addRules: {
        roleName: [{ required: true, message: '请输入角色', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '请描述该角色', trigger: 'blur' }]
      },
      // 编辑验证规则
      editRules: {
        roleName: [{ required: true, message: '请输入角色', trigger: 'blur' }],
        roleDesc: [{ required: true, message: '请描述该角色', trigger: 'blur' }]
      },
      // 用户添加
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // 用户编辑
      editForm: {}
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 获取角色列表
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.console.error('获取角色列表失败')
      }
      this.rolesList = res.data
    },
    // 移除权限
    async removeTag(role, rightId) {
      const confirmResult = await this.$confirm('此操作将无法撤消!', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warnning'
      }).catch((err) => err)
      // 未移除
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消')
      }
      // 移除并发起请求
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.getRolesList()
      this.$message.success('删除成功')
    },
    // 更改对话框状态
    async setDialogVisible(role) {
      this.roleId = role.id
      this.dialogVisible = !this.dialogVisible
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('获取权限数据失败')
      }
      this.rightsList = res.data
      this.getLeafKeys(role, this.defKeys)
    },
    // 获取三级节点
    getLeafKeys(node, arr) {
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach((item) => {
        this.getLeafKeys(item, arr)
      })
    },
    // 重置keys数组
    resetDialog() {
      this.defKeys = []
      this.roleId = []
    },
    // 分配权限
    async attributeRights() {
      const uKeys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const keyStr = uKeys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: keyStr }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('分配权限失败')
      }
      this.getRolesList()
      this.dialogVisible = false
      this.$message.success('分配成功')
    },
    // 添加角色对话框
    addRole() {
      this.roleDialogVisible = true
    },
    // 添加角色
    async confirmAddRole() {
      const { data: res } = await this.$http.post('roles', this.addForm)
      if (res.meta.status !== 201) {
        return this.$message.error('添加失败')
      }
      this.$message.success('添加成功')
      this.getRolesList()
      this.roleDialogVisible = false
    },
    // 编辑角色对话框
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色数据失败')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 确认编辑
    confirmEditRole() {
      // 预验证
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 通过验证 发起请求
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          { roleName: this.editForm.roleName, roleDesc: this.editForm.roleDesc }
        )
        if (res.meta.status !== 200) {
          this.$message.error('编辑失败')
        }
        this.getRolesList()
        this.editDialogVisible = false
        this.$message.success('编辑成功')
      })
    },
    // 删除角色
    async deleteRole(id) {
      const confirmResult = await this.$confirm(
        '该操作无法撤销,是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消')
      }
      // 确认删除 发起请求
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.getRolesList()
      this.$message.success('删除成功')
    }
  }
}
