export default {
  data() {
    // 验证邮箱的规则
    var checkEmail = (rule, value, callback) => {
      const regEmail = /^([a-zA-z0-9_-])+@([a-zA-z0-9_-])+(\.[a-zA-z0-9_-])+/
      // 通过验证
      if (regEmail.test(value)) {
        return callback()
      }
      // 不通过
      callback(new Error('请输入正确的邮箱地址'))
    }
    // 验证手机号的规则
    var checkMobile = (rule, value, callback) => {
      const regMobile =
        /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
      // 通过验证
      if (regMobile.test(value)) {
        return callback()
      }
      // 不通过
      callback(new Error('请输入正确的手机号码'))
    }
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userlist: [],
      total: 0,
      addDialogVisible: false,
      editDialogVisible: false,
      attributeDialogVisible: false,
      // 添加 表单数据
      addForm: {
        username: '',
        password: '',
        mobile: '',
        email: ''
      },
      // 编辑 表单数据
      editForm: {},
      // 分配 表单数据
      attributeForm: {},
      // 角色列表
      roleList: [],
      // 选中的角色id
      attributeList: [],
      // 添加表单的 验证规则
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, max: 10, message: '请输入2~10位有效字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 20, message: '请输入6~20位有效字符', trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '请输入电话号码', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      },
      // 修改表单的 验证规则
      editFormRules: {
        mobile: [
          { required: true, message: '电话号码不能为空', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '邮箱不能为空', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    // 获取用户列表数据
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取用户列表数据失败')
      }
      this.userlist = res.data.users
      this.total = res.data.total
    },
    // 监听 pagesize 的改变事件
    handleSizeChange(newPageSize) {
      this.queryInfo.pagesize = newPageSize
      this.getUserList()
    },
    // 监听 当前页数 的改变事件
    handleCurrentChange(newPageNum) {
      this.queryInfo.pagenum = newPageNum
      this.getUserList()
    },
    // 修改用户状态
    async changeUserState(userinfo) {
      const { data: res } = await this.$http.put(
        `users/${userinfo.id}/state/${userinfo.mg_state}`
      )
      if (res.meta.status !== 200) {
        userinfo.mg_state = !userinfo.mg_state
        return this.$message.error('更新用户状态失败!')
      }
      this.$message.success('更新用户状态成功!')
    },
    // 展示用户添加对话框
    changeAddDialogVisible() {
      this.addDialogVisible = true
    },
    // 关闭对话框实现重制
    resetAddDialog() {
      this.$refs.addFormRef.resetFields()
    },
    // 添加用户
    addUser() {
      // 表单预验证
      this.$refs.addFormRef.validate(async (valid) => {
        // 预验证未通过
        if (!valid) return
        // 通过并发起请求
        const { data: res } = await this.$http.post('users', this.addForm)
        // 添加失败
        if (res.meta.status !== 201) {
          this.$message.error('添加失败,请重试!')
        }
        this.$message.success('添加成功!')
        this.addDialogVisible = false
        // 重新获取用户列表
        this.getUserList()
      })
    },
    // 展示用户编辑对话框
    async changeEditDialogVisible(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('查询用户失败')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 编辑用户信息
    editUser() {
      // 预验证
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) return
        // 通过并发起请求
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          { mobile: this.editForm.mobile, email: this.editForm.email }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('修改失败')
        }
        this.getUserList()
        this.$message.success('修改成功!')
        this.editDialogVisible = false
      })
    },
    // 关闭编辑对话框重制状态
    resetEditDialog() {
      this.$refs.editFormRef.resetFields()
    },
    // 删除用户
    async deleteUser(id) {
      // 弹框
      const confirmResult = await this.$confirm(
        '是否确定删除该用户信息,此操作无法撤销',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        // 取消了删除操作
        this.$message.info('已取消该操作')
        return
      }
      // 确认删除 发送请求
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('删除失败')
      }
      this.getUserList()
      this.$message.success('删除成功')
    },
    // 分配角色
    async attributeDialog(editInfo) {
      this.attributeForm = editInfo
      this.attributeDialogVisible = true
      // 获取角色列表
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('获取角色列表失败')
      }
      this.roleList = res.data
    },
    // 确认分配角色
    async confirmAttribute() {
      const { data: res } = await this.$http.put(
        `users/${this.attributeForm.id}/role`,
        { rid: this.attributeList }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('分配失败')
      }
      this.getUserList()
      this.$message.success('分配成功')
      this.attributeDialogVisible = false
    },
    // 监听 分配角色对话框 关闭事件
    resetAttributeDialog() {
      this.attributeList = ''
      this.attributeForm = ''
    }
  }
}
